.header {
  background: #E4C1AE;
  margin: 0px 0px 95px 0px;
  text-align: center;
}
.header__title {
  font-size: 32px;
  margin: 0px;
  padding: 15px 0px;
}
@media (max-width: 992px) {
  .header {
    margin: 0px 0px 50px 0px;
  }
  .header__title {
    font-size: 28px;
  }
}
@media (max-width: 767px) {
  .header {
    margin: 0px 0px 30px 0px;
  }
  .header__title {
    font-size: 22px;
  }
}
@media (max-width: 480px) {
  .header__title {
    max-width: 300px;
    margin: 0 auto;
    padding: 10px 0px;
  }
}